import { defineComponent, useContext, ref } from '@nuxtjs/composition-api';
export default defineComponent({
    name: 'AdminTranslationPage',
    setup() {
        const snack = ref(false);
        const snackColor = ref('');
        const snackText = ref('');
        const { app } = useContext();
        const messages = ref([]);
        const json = ref({});
        const jsonEdited = ref({});
        const localeToDownload = ref(app.i18n.defaultLocale);
        const optionDowload = ref(jsonEdited.value);
        const search = ref();
        const load = async (code) => {
            app.i18n.setLocale(code);
            await new Promise((resolve) => {
                setTimeout(() => {
                    resolve(true);
                }, 500);
            });
            Object.keys(app.i18n.messages).forEach((code) => {
                Object.keys(app.i18n.messages[code]).forEach((key) => {
                    if (key === 'default')
                        key = '_default';
                    const existedIndex = messages.value
                        .map((i) => i.key)
                        .indexOf(key);
                    if (existedIndex >= 0) {
                        messages.value[existedIndex] = {
                            ...messages.value[existedIndex],
                            [code]: app.i18n.messages[code][key]
                        };
                    }
                    else {
                        messages.value.push({
                            key,
                            code,
                            [code]: app.i18n.messages[code][key]
                        });
                    }
                    json.value[code][key] = app.i18n.messages[code][key];
                });
            });
        };
        const headers = [
            {
                text: 'key',
                align: 'start',
                sortable: true,
                value: 'key'
            }
        ];
        app.i18n.localeCodes.forEach((code) => {
            if (!json.value[code])
                json.value[code] = {};
            load(code);
            headers.push({
                text: code,
                align: 'start',
                sortable: true,
                value: code
            });
        });
        const downloadFile = () => {
            const fileURL = window.URL.createObjectURL(new Blob([JSON.stringify(optionDowload.value[localeToDownload.value])]));
            const fileLink = document.createElement('a');
            fileLink.href = fileURL;
            fileLink.setAttribute('download', `${localeToDownload.value}.json`);
            document.body.appendChild(fileLink);
            fileLink.click();
        };
        const save = (value, key, locale) => {
            if (messages.value.find((message) => message.key === key)) {
                messages.value.find((message) => message.key === key)[locale] = value;
            }
            jsonEdited.value[locale] = jsonEdited.value[locale]
                ? jsonEdited.value[locale]
                : {};
            jsonEdited.value[locale][key] = jsonEdited.value[locale][key]
                ? jsonEdited.value[locale][key]
                : {};
            jsonEdited.value[locale][key] = value;
            snack.value = true;
            snackColor.value = 'success';
            snackText.value = 'Data saved';
        };
        const cancel = () => {
            snack.value = true;
            snackColor.value = 'error';
            snackText.value = 'Canceled';
        };
        const open = () => {
            snack.value = true;
            snackColor.value = 'info';
            snackText.value = 'Open editor';
        };
        const close = () => {
            console.log('close');
        };
        return {
            jsonEdited,
            messages,
            headers,
            search,
            json,
            localeToDownload,
            downloadFile,
            save,
            cancel,
            open,
            close,
            snack,
            snackColor,
            snackText,
            optionDowload
        };
    }
});
